//
const fetchJsonp = require('fetch-jsonp');

let search = document.querySelector('input');
let thead = document.querySelector('thead');
search.addEventListener('keyup',fetchMusic)
function fetchMusic(e){
    if(e.keyCode === 13){
        const keyWord = search.value;

        //
        if(isEmpty(keyWord)){
            showAlert();
            return;
        }

        const fetchUrl = `https://itunes.apple.com/search?term=${keyWord}&limit=25`

        fetchJsonp(fetchUrl)
        .then(function(res){
            return res.json()
        })
        .then(function(data){
           showMusic(data.results)
        })
        .catch(function (err) { alert('')})

        //input
        search.value = '';
        //thead
        thead.style.opacity = 1;
    }
    
}

const musicInfo = document.querySelector('tbody');

function showMusic(musics){
    // 
    musicInfo.innerHTML = '';
    
    for(var i=0;i<musics.length;i++){
        const info = musics[i]

        //song
        if (info.kind !== 'song'){
            continue;
        }
        console.log(info);

        const tr = document.createElement('tr');
        
        tr.innerHTML = `
            <th scope="row">
                <span class="album">
                    <img src="${info.artworkUrl100}">
                </span>
            </th>
            <td  style="padding-left: 10px"> ${info.trackName} </td>
            <td> ${info.artistName}</td>
            <td> ${info.collectionName}</td>
            <td style=" text-align: center"><audio src="${info.previewUrl}"controls></audio></td>
            <td><p><a href="${info.collectionViewUrl}" target="blank"></a></p></td>
        `;
        
        musicInfo.appendChild(tr);
    }
        // ${info.artistName} 
        // ${info.trackName}  
        // ${info.artworkUrl100} 
        // ${info.collectionName} 
        // ${info.releaseDate.split('-', 1)} 
        // ${info.previewUrl} 
        // ${info.collectionViewUrl} 
}
